*{
    padding: 0;
    margin: 0;
}

body{
    overflow: hidden;
}

.Home{
    background-color: #272B3B;
    height: 100vh;
    width: 100vw;
    /* box-sizing: border-box; */
    overflow: hidden;

}

.header-page{
    width: 100%;
    height: calc(140px - 5%);
    max-height: 140px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.title{
    font-family: "rockwell";
    color: #8EA7D4;
    font-size: calc(40px - 1vw);
    font-weight: bold;
    margin-left: 70px;
}
.social-media{
    margin-right: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 300;
}

.social-media a img{
    width: 55px;
    height: 55px;
    margin-left: 10px;
    transition: 0.5s;
}


.social-media a:hover img{
    width: 65px;
    height: 65px;
    transition: 0.5s;
} 

/* =================================================== */

.content{
    width: 100%;
    display:inline-flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

/* ==================== pets images ======================= */
.Images-content{
    margin-top: 70px;
    height: 350px;
    display: flex;
    justify-content: space-between;
    z-index: 3;
}
.Images-content img:nth-child(1){
    width: 164px;
    height: 164px;
    object-fit: cover;
    border-radius: 32px;
 
    margin-top: calc(164px/1 - 20px);
    transition: 0.5s;
}
.Images-content img:nth-child(2){
    width: 274px;
    height: 274px;
    object-fit: cover;
    border-radius: 24px;
    transition: 0.5s;
    margin-right: 166px;
    margin-left: 166px;
}

.Images-content img:nth-child(3){
    width: 164px;
    height: 164px;
    object-fit: cover;
    border-radius: 32px;

    margin-top: calc(164px/1 - 20px);
    transition: 0.5s;
}

/* aumenta su tamano suave al pasar el mouse */
.Images-content img:nth-child(1):hover{
    width: 200px;
    height: 200px;
    transition: 0.5s;
}
.Images-content img:nth-child(2):hover{
    width: 300px;
    height: 300px;
    transition: 0.5s;
}


.Images-content img:nth-child(3):hover{
    width: 200px;
    height: 200px;
    transition: 0.5s;
}

/* =========================Button======================= */

.btn-content{
    margin-top: 0px;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    height: 270px;
    z-index: 4;
    flex-direction: column;
}

.btn-content__btn{
    width: 233px;
    height: 80px;
    border-radius: 40px;
    border: none;
    background-color: #2D7AFF;
    color: #ffffff;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    font-size: "Tw Cen MT";

    margin-top: 50px;
}


.btn-content p{
    font-family: "verdana";
    color: #ffffff;
    font-size: 22px;
    margin-top: 40px;
    text-align: center;
    opacity: 0.2;
}

.twentytwo{

    font-family: "verdana";

    color: #427ce9;

    font-size: 22px;
    text-decoration: none;
    opacity: 1;
}

/* ======================================= */
footer{
    width: 100%;
    height: 50px;
    z-index: 5;
    position: absolute;
    text-align: center;
    color: #ffffff;
    font-size: 16px;
    font-weight: bold;
    font-style: italic;
    font-family: Verdana;
    opacity: 0.2;
    bottom: 0;
}

.background-ball{
    width: 100vw;
    height: 100vh;
    z-index: 1;
    position: absolute;
    bottom: 0;
    overflow: hidden;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; 
}

.background-ball img{
    width: 100vw;

    object-fit: cover;
    top: 65vh;
    position: absolute;
    z-index: -1;

}


.btn-content__btn:hover{
    opacity: 0.5;
}

.btn-content__btn:active{
    transform: scale(0.9);
}



/* media querys */
@media  screen and (max-width: 1300px){

    .header-page{
        height: 100px;
    
    }
    .Images-content{
        height: 220px;
    }

    .Images-content img:nth-child(1){
        width: 80px;
        height: 80px;
        margin-top: calc(80px/1 - 20px);
        border-radius: 15px;
    }
    .Images-content img:nth-child(2){
        width: 160px;
        height: 160px;
        margin-right: 60px;
        margin-left: 60px;
    }
    .Images-content img:nth-child(3){
        width: 80px;
        height: 80px;
        margin-top: calc(80px/1 - 20px);
        border-radius: 15px;
    }
    .btn-content{
        height: 100px;
    }
    .btn-content__btn{
        width: 140px;
        height: 50px;
        font-size: 16px;
        margin-top: 10px;
    }
    .btn-content p{
        font-size: 15px;
        margin-top: 10px;
    }
    footer{
        font-size: 10px;
    }

    
    .twentytwo{
        font-size: 17px;
    }
}
    



@media screen and (max-width: 800px){

    .title{
        font-size: 15px;
        margin-left: 25px;
    }
    
    .social-media{
        margin-right: 25px;
    }

    .social-media a img{
        width: 40px;
        height: 40px;
        margin-left: 8px;
    }

    .Images-content{
        height: calc(200px - 5vh);
    }

    .Images-content img:nth-child(1){
        width: 70px;
        height: 70px;
        margin-top: calc(100px/1 - 20px);
        border-radius: 15px;
    }
    .Images-content img:nth-child(2){
        width: 110px;
        height: 110px;
        margin-right: 30px;
        margin-left: 30px;
    }
    .Images-content img:nth-child(3){
        width: 70px;
        height: 70px;
        margin-top: calc(100px/1 - 20px);
        border-radius: 15px;
    }

    .Images-content img:nth-child(1):hover{
        width: 80px;
        height: 80px;
        transition: 0.5s;
    }
    .Images-content img:nth-child(2):hover{
        width: 130px;
        height: 130px;
        transition: 0.5s;
    }
    
    
    .Images-content img:nth-child(3):hover{
        width: 80px;
        height: 80px;
        transition: 0.5s;
    }


    
    .twentytwo{
        font-size: 14px;
    }




    .btn-content{
        height: 150px;
    }
    .btn-content__btn{
        width: 150px;
        height: 50px;
        font-size: 10px;
        margin-top: calc(50px/1 - 20px);
    }
    .btn-content p{
        font-size: 11px;
        margin-top: 2px;
    }

    .background-ball{
        height: 100vh;
        bottom: 0;
        z-index: 1;
        position: absolute;
        object-fit: cover;
    }
    
    .background-ball img{
        width: 100vw;
        object-fit: cover;
        top: 80vh;
        position: absolute;
        z-index: -1;

    }

    footer{
        font-size: 10px;
        height: 25px;
    }

    
}
    
