

.Mint{
    background-color: #272B3B;
    height: 100vh;
    width: 100vw;
    display: inline-flex;
    align-items: center;
    flex-direction: column;
}

.Mint-header{

    height: 120px;
    width: 100vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.twentytwo{
    font-family: "verdana";
    color: #427ce9;
    font-size: 22px;
    text-decoration: none;
    opacity: 1;
}


.Mint-header a{
    margin-top: 20px;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2D7AFF;
    border-radius: 50%;
    margin-left: 57px;
}

.Mint-content{
    width: 441px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}


.Progressbar{
    width: 50vw;
    max-width: 100%;
    border-radius: 10px;
    height: 42px;
    border: solid 3px #2D7AFF;
    display: inline-flex;
    align-items: center;
    text-align: center;
    
    box-sizing: border-box;


}

.Progressbar p{
    z-index: 301;
    font-size: 20px;
    font-family: "Microsoft YaHei";
    font-weight: bold;
    color: #ffffff;
    position: absolute;
    left: 0;
    right: 0;
  
}



.Progressbar-content{
    width: 0%;
    height: calc(100% + 2px);
    border-radius: 8px;
    background-color: #2D7AFF;
    z-index: 300;
    padding: 0;
}

.mint-app{
    margin-top: 25px;
    width: 50vw;
    height: 60vh;
    max-width: 441px;
    max-height: 559px;
    background-color: #2D3247;
    border-radius: 19px;
    box-shadow: #0000003b 0px 3px 23px ;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
}

.mint-app-sold{
    margin-top: 25px;
    width: 50vw;
    height: 60vh;
    max-width: 441px;
    max-height: 559px;
    border-radius: 19px;
    /* box-shadow: #0000003b 0px 3px 23px ; */
    display: inline-flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.sold-out-text{
    font-size: 14px;
    font-family: "Microsoft YaHei";
    font-weight: bold;
    color: #ffffff;
    margin-top: 100px;
    text-align: center;
    opacity: 0.32;

}

.sold-out{
    font-size: 67px;
    text-align: center;
    font-family: "Microsoft YaHei";
    font-weight: bold;
    color: #8EA7D4;
    opacity: 0.7;
}


.mint-app__header{
    margin-top: 50px;
}


.mint-app__content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}


.mint-app__input{
    background-color: #8EA7D4;
    border: none;
    border-radius: 9px;
    height: 61px;
    text-align: center;
    font-family: "rockwell", "Microsoft YaHei";
    font-weight: bold;
    font-size: 19px;
    color: #ffffff;
    margin-top: 33px;
}

.mint-app__input::placeholder{
    color: #ffffff;
    opacity: 0.5;

}

.mint-app__input::-webkit-outer-spin-button,::-webkit-inner-spin-button{
    -webkit-appearance: none;
}

.p-TX{
    margin-top: 17px;
    width: 100%;
    text-align: center;
    color: #ffffff;
    font-size:18;
    font-family: "Microsoft YaHei";
    font-weight: bold;
    opacity: 0.4;
}

.p-price{
    margin-top: 45px;
    width: 100%;
    text-align: center;
    color: #ffffff;
    font-size: 20px;
    font-family: "Microsoft YaHei";
    font-weight: bold;
    opacity: 0.8;
}

.garbageLogo{
    width: 141px;
    border-radius: 17px;
}

.mint-app__button{
    width: 257px;
    height: 80px;
    border-radius: 27px;
    background-color: #9C4EF6;
    color: #ffffff;
    font-family: "rockwell", sans-serif;
    border: none;
    font-weight: bold;
    font-size: 20px;
    margin-top: 24px;
}

.mint-app__button:hover{
    background-color: #9538ff;
}
.mint-app__button:active{
    transform: scale(0.9);
};

.Mint-social-media{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.Mint-social-media a img{
    width: 50px;
    height: 50px;
    margin-left: 18px;
    transition: 0.5s;
}


.Mint-social-media a:hover img{
    width: 65px;
    height: 65px;
    transition: 0.5s;
} 


.Mint-status{
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.Mint-status-item{
    right: 0;
    position: absolute;
    margin-right:40px;
    background-color: #9C4EF6;
    height: 80px;
    width: 269px;
    text-align: center;
    border-radius: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: bold;
    color: white ;
    font-size: 14px;
    transition: 0.5s;
}
.footer {
    margin-top:20px ;
    width: 100%;
    height: 400px;
    /* background-color: #8EA7D4; */
    text-align: center;
    display: flex;
    justify-content: center;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.footer p{
    font-size: 20px;
    font-weight: bold;
    color: #ffffff;
    opacity: 0.2;
}

